.card {
    height: 230px;
    width: 230px;
    position: relative;
    border: #88ace2 3px ridge;
    box-shadow:5px 5px 5px #888888;
    border-radius: 5px;
}

.card:hover {
    opacity: 0.35;
}
.jumbotron {
    margin-bottom: 0px;
    color: #426bda;

    background-image: url(/Clicky_Game/static/media/memoryHeader3.4e118236.jpg);
    background-repeat: repeat;
    padding: 30px 0px;
    font-family: 'Muli', sans-serif;

}


header > h1 {
    color: #2c4c7c;
    padding: 5px;
    font-family: 'Cinzel Decorative', cursive;
}
.wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
}
.score-board {
    padding: 2px 0px;
    color: #333;
    background-color: #88ace2;
    margin-bottom: 10px
}

.score-board > h3 {
    color: #f4f4f4;
}
img {
    position: absolute;
   height: 100%;
}
